export const useHttp = (fn, params) => { // fn=getAllUsers,     User,  const {data: users, loading, error} = useHttp(getAllUsers)
  const [data, setData] = useState([])
  const [loading, setLoading] =useState(false)
  const [error, setError] = useState(null)
  useEffect(()=>{
      
      const getData = async ()=> {
        setLoading(true)
          try {
              const res= await fn(params) // fn=getAllUsers, (params)- ,   params
              setData(res)
              setLoading(false)
          } catch (error){
              setError(error.message)
              setLoading(false)
  
          } 
  
      }
      getData() 
      



  }, [fn, params]) //params- ,   params
  return {data, loading, error}
}

